import Base from './Base';
import './bass.css';
class Pop extends Base{
	constructor(){
		super();
	}
	init(config){
		super.init(config);
		this.resize();
	}
	tips( obj ){
		let tips = new Base() ,
			config = {
				wrapper : 'tips',
				removeClose : true,
                paddingBottom : "0"
			};
		Object.assign(config,obj);
		tips.init(config);
		setTimeout(function(){
			tips.$pop.close();
		},obj.time || 800 );
	}
	load(){
		let load;
		return{
			add(){
				load = new Base();
				load.init({
					wrapper : 'loadding',
					removeClose : true,
					content : `
						<div class="sk-spinner sk-spinner-wave">
							<div class="sk-rect1"></div>
							<div class="sk-rect2"></div>
							<div class="sk-rect3"></div>
							<div class="sk-rect4"></div>
							<div class="sk-rect5"></div>
						</div>
					`
				});
			},
			remove(){
				if(load) load.$pop.close();
			}
		};
	}
}
